:root {
  --font-default: Arial, sans-serif;
  --gradient: linear-gradient(93deg, #fb8129 -11.01%, #cd380e 109.58%);
  --gray: #878787;
  --dark: #0f0f0f;
  --text: #0f0f0f;
  --link: #fb8129;
