:root {
  scroll-behavior: smooth;
}
html {
  margin: 0;
  padding: 0;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  font-variant-ligatures: none;
  font-family: var(--font-default);
  font-size: 1px;
  line-height: 1.3;
  font-weight: normal;
  height: 100%;
  min-height: 320px;
  min-width: 320px;
  color: #000;
  background: #fff;
}
html._has-no-visible-focus * {
  outline: 0 !important;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  position: relative;
  overflow-x: hidden;
  font-size: 18rem;
  color: #000;
}
button {
  font-family: Circe;
}
::selection {
  color: #fff;
  background: #000;
}
::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.4);
  opacity: 1;
}
::-moz-placeholder {
  color: rgba(0, 0, 0, 0.4);
  opacity: 1;
}
:disabled {
  cursor: default;
  pointer-events: none;
  opacity: 0.7;
  user-select: none;
}
img {
  margin: 0;
}
a {
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  color: currentColor;
}
a[href^="mailto"],
a[href^="tel"] {
  user-select: text;
}
nav ul {
  margin: 0;
  padding: 0;
}
button,
input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
}
textarea,
input[type="tel"],
input[type="text"],
input[type="email"],
input[type="password"] {
  -webkit-appearance: none;
}
html.desktop textarea {
  scrollbar-width: thin;
  scrollbar-color: #fff #000;
}
html.desktop textarea::-webkit-scrollbar {
  width: 12px;
  height: 12px;
  background: #000;
}
html.desktop textarea::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #fff;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
figure,
figcaption {
  margin: 0;
  padding: 0;
}
template {
  display: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
p {
  margin: 1em 0;
}
color {
  background: linear-gradient(93deg, #fb8129 -11.01%, #cd380e 109.58%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.wrap-page {
  display: flex;
  flex-direction: column;
  min-height: 100svh;
  padding-top: 160px;
}
@media (max-width: 1200px) {
  .wrap-page {
    padding-top: 87px;
  }
}
@media (max-width: 1400px) {
  .d-min-1400 {
    display: none !important;
  }
}
@media (max-width: 1200px) {
  .d-min-1200 {
    display: none !important;
  }
}
@media (min-width: 1200px) {
  .d-max-200 {
    display: none !important;
  }
}
@media (max-width: 760px) {
  .d-min-760 {
    display: none !important;
  }
}
@use "sass:math";
body {
  font-family: Circe;
}
h1,
.h1 {
  font-size: 400px;
  line-height: normal;
  font-size: clamp(24px, calc(24px + 22 * ((100vw - 375px) / 1065)), 46px);
  font-family: Unbounded;
  font-weight: 700;
  letter-spacing: -0.46px;
}
h3,
.h3 {
  font-family: Unbounded;
  font-size: clamp(20px, calc(20px + 4 * ((100vw - 375px) / 1065)), 24px);
  font-style: normal;
  font-weight: 500;
  margin-top: 40px;
  margin-bottom: 24px;
  line-height: 133.333%;
}
.text {
  max-width: 909px;
}
.text a {
  color: var(--link);
}
.wrap-page {
  overflow: hidden;
}
.inner {
  padding-left: var(--inner-padding);
  padding-right: var(--inner-padding);
}
.btn-border {
  border: 1px solid;
  display: flex;
  height: 48px;
  padding: 10px 20px 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 50px;
}
.breadcrumbs {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-bottom: 30px;
  padding: 0;
  overflow-x: auto;
  overflow-y: hidden;
  max-width: calc(100% + (var(--inner-padding) * 2));
  margin-left: calc(-1 * var(--inner-padding));
  margin-right: calc(-1 * var(--inner-padding));
}
.breadcrumbs::-webkit-scrollbar {
  width: 0;
  height: 0;
}
.breadcrumbs::before,
.breadcrumbs::after {
  min-width: var(--inner-padding);
  height: 6px;
  display: block;
  content: "";
}
.breadcrumbs li {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 136%;
  color: var(--gray);
  white-space: nowrap;
}
.breadcrumbs li:last-child::after {
  display: none;
}
.breadcrumbs li a {
  color: var(--link);
}
.breadcrumbs li::after {
  display: inline-block;
  content: "/";
  color: var(--link);
  margin-left: 8px;
  margin-right: 8px;
}
.header {
  --color: #0f0f0f;
  --line: #cfcfcf;
  color: var(--color);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
}
.header .inner {
  background: #fff;
  position: relative;
  z-index: 3;
}
.header__overlay {
  z-index: 1;
  width: 100vw;
  position: absolute;
  pointer-events: none;
  height: 100vh;
  opacity: 0;
  transition: opacity 0.3;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(50px);
}
header._overlay-visible .header__overlay {
  opacity: 1;
  pointer-events: all;
}
.header__top {
  padding-top: 26px;
  padding-bottom: 26px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  border-bottom: 1px solid var(--line);
}
.header__lang-current {
  background: transparent;
  padding: 0;
  border: 0 solid;
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}
.header__lang-current:hover {
  color: var(--link);
}
.header__lang-list {
  display: none;
}
.header__left-menu,
.header__menu {
  padding: 0;
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 20px;
  margin: 0;
  align-items: center;
}
.header__left-menu li,
.header__menu li {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}
.header__left-menu a,
.header__menu a {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
}
.header__left-menu a:hover,
.header__menu a:hover {
  color: var(--link);
}
.header__left-menu {
  margin-right: auto;
}
.header__btns {
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.header__logo {
  margin-right: auto;
}
@media (max-width: 1200px) {
  .header__logo {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
  }
}
.header__right {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 30px;
}
.header__sertificat {
  border: 1px solid;
  display: flex;
  height: 48px;
  padding: 10px 20px 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 50px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  /**/
}
.header__buy-bilet {
  background: var(--gradient);
  display: flex;
  height: 48px;
  padding: 10px 20px 10px 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: #fff;
  border-radius: 50px;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  /**/
}
.header__burger {
  width: 18px;
  height: 14px;
  border: 0 solid;
  background: transparent;
  position: relative;
}
.header__burger span {
  height: 2px;
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  background: var(--dark);
  transition: margin 0.3s, opacity 0.3s, transform 0.3s;
}
.header__burger span:first-child {
  transform: translateY(-8px);
}
._burder-menu-open .header__burger span:first-child {
  transform: rotate(-45deg);
}
._burder-menu-open .header__burger span:nth-child(2) {
  opacity: 0;
}
.header__burger span:last-child {
  transform: translateY(8px);
}
._burder-menu-open .header__burger span:last-child {
  transform: rotate(45deg);
}
.main-menu {
  position: relative;
}
@media (max-width: 1200px) {
  .main-menu {
    max-height: 0;
    height: calc(100svh - 77px);
    transition: max-height 0.6s;
    overflow-x: auto;
  }
  ._burder-menu-open .main-menu {
    max-height: calc(100svh - 77px);
  }
}
.main-menu__sub-back {
  border: 0 solid;
  padding: 0;
  background: transparent;
  font-family: Circe;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 30px;
}
.main-menu__sub-back svg {
  position: relative;
  top: -1px;
}
.main-menu__menu {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-between;
}
@media (max-width: 1200px) {
  .main-menu__menu {
    flex-direction: column;
    margin-top: 40px;
    gap: 24px;
  }
  body:has(.main-menu__sub._active) .main-menu__menu {
    opacity: 0;
  }
}
.main-menu__menu a {
  color: var(--color);
  font-size: 14px;
  font-style: normal;
  padding: 15px 0;
  font-weight: 700;
  line-height: 171.429%;
  text-transform: uppercase;
  position: relative;
  display: block;
}
@media (max-width: 1200px) {
  .main-menu__menu a {
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    line-height: normal;
  }
  .main-menu__menu a::before {
    display: none !important;
  }
  .main-menu__menu a::after {
    margin-left: 10px;
    display: inline-block;
    height: 18px;
    position: relative;
    top: -1px;
